import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { getLeaderboard } from "../../app/api/past-tense";
import { AppThunk, RootState } from "../../app/store";
import { LeaderBoard } from "./model";

const initialState: LeaderBoard.initialState = {
  data: [],
  isLoading: false,
  hasError: false,
};

export const leadboardSlice = createSlice({
  name: "leaderboard",
  initialState,
  reducers: {
    fetchLeaderBoard: (state) => {
      state.isLoading = true;
      state.hasError = false;
    },
    fetchLeaderBoardSuccess: (
      state,
      action: PayloadAction<LeaderBoard.Score[]>
    ) => {
      state.data = action.payload;
      state.isLoading = false;
      state.hasError = false;
    },
    fetchLeaderBoardFailed: (state) => {
      state.isLoading = false;
      state.hasError = true;
    },
    setLeaderBoard: (state, action: PayloadAction<LeaderBoard.Score[]>) => {
      state.data = action.payload;
    },
    resetLeaderBoard: (state) => initialState,
  },
});

export const {
  fetchLeaderBoard,
  fetchLeaderBoardFailed,
  fetchLeaderBoardSuccess,
  resetLeaderBoard,
  setLeaderBoard,
} = leadboardSlice.actions;

export const selectLeaderBoard = (state: RootState) => state.leaderboard;

export default leadboardSlice.reducer;

export const loadLeaderBoard = (): AppThunk => async (dispatch) => {
  dispatch(fetchLeaderBoard());
  try {
    const response = await getLeaderboard();

    const { data } = response.data;
    dispatch(
      fetchLeaderBoardSuccess(
        data.sort((first, second) => second.score - first.score)
      )
    );
  } catch (error) {
    dispatch(fetchLeaderBoardFailed());
  }
};
