import React, { useState } from "react";
import Modal from "../Modal";
import "./styles.scss";

type UserDetailProps = {
  open: boolean;
  onClose?: () => void;
  onSubmit: (name: string, phone: string) => void;
};

const UserDetailsModal: React.FC<UserDetailProps> = ({
  open,
  onClose,
  onSubmit,
}) => {
  const [name, setName] = useState<string>("");
  const [phone, setPhone] = useState<string>("");
  const handleSubmit = () => {
    onSubmit(name!, phone!);
  };
  return (
    <Modal open={open} onClose={onClose}>
      <h6 className="modal-title">Enter details to see your rank</h6>
      <div className="form">
        <form>
          <div className="form-group mb-sm-3">
            <input
              type="text"
              id="name"
              name="name"
              placeholder=" "
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <label className="floating-label">Your Name</label>
          </div>

          <div className="form-group mb-sm-3">
            <input
              type="tel"
              id="phone"
              name="phone"
              placeholder=" "
              pattern="[789][0-9]\d{8,11}$"
              required
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
            />
            <label className="floating-label">Phone Number</label>
          </div>
        </form>
        <div className="btn-container">
          <button className="btn" onClick={() => onClose?.()}>
            Cancel
          </button>
          <button
            className="btn submit"
            onClick={handleSubmit}
            disabled={!name || !phone || !/[789][0-9]\d{8,11}/.test(phone)}
          >
            Submit
          </button>
        </div>
      </div>
    </Modal>
  );
};

export default UserDetailsModal;
